.loginpage{
    margin-left: auto;
    margin-right: auto;
    padding:0;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
    
}
.login{
    appearance: none;
    background: none;
    border: none;
    outline: none;
    
}

.loginform{
    display: flex;
    position: relative;
    top:5vh;

   
}

.loginform:after{
    content: '';
    display: block;
    position: absolute;
    top:-5px;
    left:-5px;
    right:-5px;
    bottom:-5px;
    z-index: -1;
    background-image: linear-gradient(to bottom right,#FFCE00,#FE4880);

}

.loginform .form-inner{
    position: relative;
    display: block;
    background-color: #FFF;
    padding: 30px;
    z-index: 2;
}

.loginform .form-inner h2{
    color:#888;
    font-size: 28px;
    font-weight: 500;
    margin-bottom: 30px;
}

.loginform .form-inner .form-group{
    display: block;
    width: 300px;
    margin-bottom: 15px;
}

.form-inner .form-group label{
    display: block;
    color:#666;
    font-size: 12px;
    margin-bottom: 5px;
    transition: 0.4s;
}

.form-inner .form-group:focus-within label{
    color: #FE4880;
}

.loginform .form-inner .form-group input{
    display: block;
    width: 100%;
    padding: 10px 15px;
    background-color: #F8F8F8;
    border-radius: 8px;
    transition: 0.4s;
}

.loginform .form-inner .form-group input:focus{
    box-shadow: 0px 0px 3px rgba(0,0,0,0.2, green, blue, alpha);
}

.loginform .form-inner input[type="submit"], .welcome button{
    display: inline-block;
    padding: 10px 15px;

    border-radius: 8px;
    background-image: linear-gradient(to right,#FFCE00 50%,#FFCE00 50%, #FE4880);
    background-size: 200%;
    background-position: 0%;
    transition: 0.4s;
    color:#FFF;
    font-weight: 700;
    cursor: pointer;
}

.loginform .form-inner input[type="submit"]:hover, .welcome button:hover{
    background-position: 100% 0%;
}


.welcome {
    width: 100%;
    max-width: 480px;
    background-color: #FFF;
    padding: 15px 30px;
}

.welcome h2{
    color:#888;
    font-size: 28px;
    font-weight: 500;
    margin-bottom: 30px;
}

.welcome h2 span{
    color:#FE4880;
    font-weight: 700;
}

@media screen and (max-width: 500px) {
    .loginform .form-inner .form-group{
        display: block;
        width: 80vw;
        margin-bottom: 15px;
    }
    .loginform .form-inner{
        padding: 15px;
     
    }
}