.FuncBox{
    width: 25vh;
    height: 25vh;
    margin:2rem 2rem 2rem 0;
    padding: 1.5rem;
   
    box-shadow: 0 0 10px 4px rgba(0,0,0,1);
    transition: transform .3s ease;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    

}
.FuncBoxContainer{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.FuncBox:hover{
    transform: scale(1.1);
}
.FuncboxText{
    
    font-weight:800;
    font-size:1.5rem;
}